<template>
	<view class="container home">
		
		<!-- 轮播 -->
		<swiper class="swiper" 
			:indicator-dots="indicatorDots" 
			:autoplay="autoplay" 
			:interval="interval" 
			:duration="duration"
			:circular="circular"
			indicator-active-color="#FFA500">
			<swiper-item v-for="(item,idx) of foodPics" :key="idx">
				<view class="swiper-item uni-bg-red">
					<image class="food-item" :src="`../../static/image/${item.name}.jpg`" mode="heightFixed"></image>
				</view>
			</swiper-item>
		</swiper>
		<!-- 轮播结束 -->
		
		<!-- 店铺信息 -->
		<view class="store-info-wrap">
			<view class="store-name"><text>商水县金记胡辣汤</text></view>
			<view class="store-detail flex">
				<view class="detail-lf flex flex-direction-column flex-justify-center" @click="openLocation">
					<view class="flex"><uni-icons class="uni-icon" type="shop" size="16" color="#999"></uni-icons>营业时间：<text>05:00-11:00</text></view>
					<view class="flex addr"><uni-icons class="uni-icon" type="location" size="16" color="#999"></uni-icons>新城路北段路西金记胡辣汤</view>
				</view>
				<view class="divider"></view>
				<view class="detail-rt flex flex-direction-column flex-align-center" @click="callPhone">
					<view><uni-icons type="phone-filled" size="20" color="#999"></uni-icons></view>
					<view>电话</view>
				</view>
			</view>
			
		</view>
		<!-- 店铺信息结束 -->
	</view>
</template>

<script>
	import uniIcon from '@/components/uni-icons/uni-icons.vue';
	import { location, phoneNumber } from '@/config/store_info.js';
	const foodPics = [
		{
			name: 'hulatang',
			id: 1
		},
		{
			name: 'babaozhou',
			id: 2
		},
		{
			name: 'xiaomizhou',
			id: 3
		},
		{
			name: 'baozi',
			id: 4
		},
		{
			name: 'shuijianbao',
			id: 5
		},
		{
			name: 'qiancengbing',
			id: 6
		},
		{
			name: 'youmotou',
			id: 7
		},
	]
	// 阻止重复打开位置
	let openw = false;
	export default {
		data() {
			return {
				indicatorDots: true,
				autoplay: true,
				interval: 3500,
				duration: 600,
				circular: true,
				foodPics: foodPics
			}
		},
		components: { uniIcon },
		methods: {
			openLocation() {
				if (openw) {
					return;
				}
				openw = true;
				uni.openLocation({
				    latitude: location.latitude,
				    longitude: location.longitude,
					name: '金记胡辣汤',
					address: '新城路北段路西',
				    success: function () {
				        console.log('success');
				    },
					complete() {
						openw = false;
					},
					fail() {
						console.log('fail');
					}
				});
			},
			// 打电话
			callPhone() {
				if (openw) {
					return;
				}
				openw = true;
				uni.makePhoneCall({
					phoneNumber: '18939600055',
					success() {
						console.log('success');
					},
					complete() {
						openw = false;
						console.log('complete');
					},
					fail() {
						console.log('fail');
					}
				})
			}
		}
	}
</script>

<style lang="less" scoped>
	.container.home {
		.swiper {
			height: 360rpx;
			.food-item {
				width: 100%;
				height: 360rpx;
			}
		}
		.store-info-wrap {
			.store-name {
				text-align: center;
				font-size: 18px;
				padding: 25rpx 0 30rpx;
			}
			.store-detail {
				color: #999;
				font-size: 12px;
			}
			.detail-lf {
				flex: 1;
				padding-left: 30rpx;
			}
			.detail-rt {
				width: 140rpx;
			}
			.uni-icon {
				margin-right: 25rpx;
			}
			.addr {
				margin-top: 8rpx;
			}
		}
	}
</style>
